export default {
	data() {
		return {
			couponValidList:[],
			couponinvalidList:[],
			headerTop:0,
			//控制滑动效果
			typeClass:'valid',
			subState:'',
			theIndex:null,
			oldIndex:null,
			isStop:false,
			typeIndex:1
		}
	},
	onPageScroll(e){
		
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
	    setTimeout(function () {
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	onLoad() {
		this.getCoupon()
		//兼容H5下排序栏位置










	},
	methods: {
		switchType(type){
			if(type=='valid'){
				this.typeIndex=1
			}else{
				this.typeIndex=2
			}
			this.getCoupon();
			if(this.typeClass==type){
				return ;
			}
			uni.pageScrollTo({
				scrollTop:0,
				duration:0
			})
			this.typeClass = type;
			this.subState = this.typeClass==''?'':'show'+type;
			setTimeout(()=>{
				this.oldIndex = null;
				this.theIndex = null;
				this.subState = this.typeClass=='valid'?'':this.subState;
			},200)
		},
		//控制左滑删除效果-begin
		touchStart(index,event){
			//多点触控不触发
			if(event.touches.length>1){
				this.isStop = true;
				return ;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX,event.touches[0].pageY];
		},
		touchMove(index,event){
			//多点触控不触发
			if(event.touches.length>1){
				this.isStop = true;
				return ;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];
			
			if(this.isStop||Math.abs(moveX)<5){
				return ;
			}
			if (Math.abs(moveY) > Math.abs(moveX)){
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}
			
			if(moveX<0){
				this.theIndex = index;
				this.isStop = true;
			}else if(moveX>0){
				if(this.theIndex!=null&&this.oldIndex==this.theIndex){
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(()=>{
						this.oldIndex = null;
					},150)
				}
			}
		},
		
		touchEnd(index,$event){
			//解除禁止触发状态
			this.isStop = false;
		},
		// 获取优惠券
		getCoupon(){
			let data={
				type:this.typeIndex
			};
			this.$xm.post('/Coupon/index',data,(res)=>{
				if(res.data.length>0){
					res.data.map(ele=>{
						ele.start_time=this.$xm.changeDay(ele.start_time*1000);
						ele.end_time=this.$xm.changeDay(ele.end_time*1000)
					})
				}
				if(this.typeIndex==1){
					this.couponValidList=res.data;
				}else if(this.typeIndex==2){
					this.couponinvalidList=res.data;
				}
			})
		},
		//删除商品
		deleteCoupon(id,List){
			let len = List.length;
			for(let i=0;i<len;i++){
				if(id==List[i].id){
					List.splice(i, 1);
					break;
				}
			}
			this.oldIndex = null;
			this.theIndex = null;
		},
		
		discard() {
			//丢弃
		}
		
		
	}
}
